import {lazy, useEffect, useState} from "react";
import FolderImage from "../assets/images/document-file-folder-500x500.jpg";
import styled from "styled-components";
import useLinkerFileViewer from "../hooks/LinkerFileViewer.jsx";
import {getLastVersion} from "../any/versions.jsx";
import HolidaysTimerLogistics from "../components/HolidaysTimerLogistics.jsx";
import ContentViewer from "../components/layout/ContentViewer.jsx";


const Header = lazy(() => import("../components/Header"));
const FlexBox = lazy(() => import("../components/structure/FlexBox"));
const Title = lazy(() => import("../components/structure/Title"));
const Main = lazy(() => import("../components/structure/Main"));
const Card = lazy(() => import("../components/structure/Card"));
export default function MoScreen() {
    const [list, setList] = useState([]);
    const onViewFileHandler = useLinkerFileViewer();

    useEffect(() => {
        async function getData() {
            try {
                const request = await fetch(`/data/mo-data.json?v=${getLastVersion()}`);
                const response = await request.json();
                setList(response);
            } catch (error) {
                console.log('error')
            } finally {
                //
            }
        }

        getData();

    }, [])
    return (
        <ContentViewer title={'Работа методического объединения'}>
            <Main>
                <Title>Документы</Title>
                <FlexBox type={`rows`}>
                    {list.length > 0 && list.map((item, index) => (
                        <Card key={index}>
                            <MaterialsCard>
                                <div className="materials-image-box">
                                    <img src={FolderImage} alt={`DOC`}/>
                                </div>
                                <div className="materials-content-box">
                                    <div className="materials-content-box-title">
                                        {item.title}
                                    </div>
                                    <div className="materials-content-box-actions">
                                        <a href={item.file}>Скачать</a>
                                        {/*item.file.split('.').indexOf('Ass22pdf') !== -1 && (
                                            <button onClick={() => onViewFileHandler(item.title, item.file)}>
                                                Просмотр
                                            </button>
                                        )*/}

                                    </div>
                                </div>

                            </MaterialsCard>
                        </Card>
                    ))}
                    {list.length === 0 && (
                        <p>Список пуст</p>
                    )}
                </FlexBox>
            </Main>
        </ContentViewer>
    )
}

const MaterialsCard = styled.div`
    display: flex;
    position: relative;
    flex-direction: row;
    justify-content: stretch;
    align-items: center;
    flex-wrap: wrap;
    width: 100%;
    @media screen and (max-width: 600px) {
        flex-direction: column;
    }

    & > div.materials-image-box {
        display: flex;
        position: relative;
        flex-direction: row;
        justify-content: center;
        align-items: center;
        width: calc(100% / 3);
        height: 100%;
        @media screen and (max-width: 600px) {
            width: 100%;
            height: 60px;
            padding: 1em 0;
        }

        & > img {
            display: block;
            position: relative;
            width: 90%;
            height: 100%;
            object-fit: contain;
        }
    }

    & > div.materials-content-box {
        display: flex;
        position: relative;
        flex-direction: column;
        width: calc(100% * 2 / 3);

        @media screen and (max-width: 600px) {
            width: 100%;
        }

        & > div.materials-content-box-title {
            color: #2f4858;
            font-weight: bold;
            text-transform: uppercase;
            margin-bottom: 1em;
            margin-left: 30px;
            text-align: justify-all;
            font-size: 20px;
        }

        & > div.materials-content-box-actions {
            display: flex;
            flex-direction: row;
            position: relative;
            flex-wrap: wrap;
            justify-content: flex-end;
            @media screen and (max-width: 600px) {
                justify-content: center;
            }

            & > a, & > button {
                margin: 5px;
                border: none;
                font-size: 16px;
                cursor: pointer;
                color: #FFFFFF;
                background-color: rgb(33, 150, 83);
                text-decoration: none;
                padding: 10px 20px;
                border-radius: 4px;
                transition: all 0.2s ease;

                &:hover {
                    transform: scale(1.01);
                    filter: drop-shadow(3px 3px 5px rgba(33, 150, 83, 0.5));
                }

                &:active {
                    transform: scale(0.99);
                    filter: drop-shadow(3px 3px 5px rgba(33, 150, 83, 0.15));
                }
            }
        }
    }
`;