import {Link, useNavigate} from "react-router-dom";
import {toast} from "react-toastify";
import {VIEWER_DOCUMENT_SESSION_STORAGE} from "../Screens/ViewerDocumentScreen.jsx";

export default function useLinkerFileViewer() {
    const navigate = useNavigate();

    function linkerFileViewer(title, link) {
        // alert
        toast.info('Идет обработка. Дождитесь загрузки файла.')
        sessionStorage.setItem(
            VIEWER_DOCUMENT_SESSION_STORAGE,
            JSON.stringify({title, link})
        );
        navigate('/viewer-file-pdf-page');

    }
    return linkerFileViewer;
}